:global(#app) {
  .controls {
    clear: both;
    margin-top: 6px;
  }

  .field {
    background: #fff;
    border: 1px solid rgba(9, 30, 66, 0.08);
    border-radius: 3px;
    color: #17394d;
    display: block;
    line-height: 1.5;
    font-size: 14px;
    margin-bottom: 4px;
    overflow: hidden;
    padding: 8px 12px;
    resize: none;
  }

  .button {
    background: rgba(9, 30, 66, 0.06);;
    font-weight: 400;
    line-height: 28px;
    min-height: auto;
    opacity: 1;
    padding: 0 6px;
    width: 28px;

    &:hover {
      background: rgba(9, 30, 66, 0.1);
    }
  }

  .taskduedate {
    width: auto;
    min-width: 28px;
  }

  .taskuser {
    padding: 0;
    &:hover {
      opacity: 0.9;
    }
  }

  .wrapper {
    margin-top: 6px;
    padding-bottom: 8px;
  }
}
