:global(#app) {
	.search {
	  margin-top: 6px;

	  >div:last-child {
		overflow-y: auto;
		max-height: calc(100vh - 60px);

		&::-webkit-scrollbar {
			width: 5px;
			height: 5px;
		  }
	  
		  &::-webkit-scrollbar-track {
			background: transparent;
		  }
	  
		  &::-webkit-scrollbar-thumb {
			border-radius: 3px;
		  }
	  }
	  
	  >div>div>div>div>div {
		  padding: 4px 8px;
	  }

	  .description {
		  font-size: 0.85em;
		  font-weight: 300;
		  color:gray;
		  display: block;
	  }
	}
  
	.searchUser {
	  color: #fff;
	  display: inline-block;
	  text-align: center;
	  height: 20px;
	  line-height: 20px;
	  width: auto;
	  padding: 0 4px;
	  margin-left: 2px;
	}

	/* Backgrounds */

	.backgroundEmerald {
		background: #2ecc71;
	  }
	
	  .backgroundPeterRiver {
		background: #3498db;
	  }
	
	  .backgroundWisteria {
		background: #8e44ad;
	  }
	
	  .backgroundCarrot {
		background: #e67e22;
	  }
	
	  .backgroundAlizarin {
		background: #e74c3c;
	  }
	
	  .backgroundTurquoise {
		background: #1abc9c;
	  }
	
	  .backgroundMidnightBlue {
		background: #2c3e50;
	  }
  }
  