:global(#app) {
  .actionButton {
    display: block;
    float:right;
    min-width: 84px;
    padding-top: 8px;
    text-align: right;
    height: 100%;
  }
  .button {
    background: rgba(9, 30, 66, 0.06);;
    box-shadow: none;
    line-height: 24px;
    min-height: auto;
    opacity: 0;
    padding: 0;
    width: 24px;

    &:hover {
      background: rgba(9, 30, 66, 0.1);
    }
  }

  .fixtarget {
    opacity: 1;
  }

  .taskuser {
    &:hover {
      opacity: 0.9;
    }
  }

  .approve {
    margin-right: 2px;
  }

  .checkboxWrapper {
    display: inline-block;
    padding: 10px 15px 0px 8px;
    position: absolute;
    text-align: center;
    top: 0;
    left: 0;
    vertical-align: top;
    z-index: 2000;
    line-height: 1;
    height: 32px;
  }

  .content:hover {
    background: rgba(9, 30, 66, 0.04);

    .target {
      opacity: 1;
    }
  }

  .task{
    overflow: hidden;
    overflow-wrap: break-word;
    padding: 8px 0;
    vertical-align: top;
    display: flex;
  }

  .taskCompleted {
    text-decoration: line-through;
  }

  .text {
    background: transparent;
    border-radius: 3px;
    color: #17394d;
    cursor: pointer;
    display: inline-block;
    overflow: hidden;
    font-size: 15px;
    line-height: 1.5;
    min-height: 32px;
    padding: 0 0 0 40px;
    width: 100%;
  }

  .wrapper {
    border-radius: 3px;
    margin-left: -40px;
    min-height: 32px;
    position: relative;
    transition: all 0.14s ease-in;
    width: calc(100% + 40px);
  }

  .attachment {
    display: inline-block;
    line-height: 0;
    margin: 0 0 6px 0;
    max-width: 100%;
    vertical-align: top;
  }

  .attachmentLeft {
    margin-right: 4px;
  }

}
