.timeLine {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  font-size: 12px;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  min-height: calc(100vh - 60px);
  padding-bottom: 70px;
}
.timeLine-header-wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  font-size: 12px;
  position: sticky;
  top: 0;
  background-color: #fff;
  border-bottom: 1px solid rgb(207, 207, 205);
  z-index: 99;
}
.timeLine-header-wrapper .timeLine-sidebar-wrapper {
  padding-top: 20px;
}
/* Main Area */

.timeLine-main {
  flex: 1 1 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
  max-width: 100vw;
}
/* Main Area Header*/

.timeLine-main-header-viewPort {
  flex: 0 0 40px;
  position: relative;
  height: 40px;
  width: 100%;
  background-color: rgb(112, 112, 112);
  overflow: hidden;
}

.timeLine-main-header-container {
  flex: 0 0 40px;
  position: relative;
  top: 0;
  left: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #333333;
  overflow: hidden;
  user-select: none;
}

.timeLine-main-header-day-item {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgb(112, 112, 112);
  font-size: 10px;
  text-align: center;
  border-right: solid 1px;
  border-top: solid 1px;
  border-bottom: solid 1px;
  top: 20px;
  height: 40px;
  color: white;
  text-align: center;
}

.timeLine-main-header-top-item {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-right: solid 1px white;
  height: 20px;
  z-index: 91;
}

.timeLine-main-header-day-week {
  flex: 0 0 12px;
  padding: 4px;
  z-index: 90;
}
.time-line-container {
  width: 100%;
  min-height: 500px;
}
#divProvideWidthForLegenda {
  background-color: #3d4852;
  background-image: linear-gradient(180deg,#3d4852,#3d4852);
  padding: 10px 15px;
  color: #fff;
  font-size: 12px;
}
#divProvideWidthForLegenda table tr {
  vertical-align: top;
}
#divProvideWidthForLegenda table td {
  padding: 0 10px;
  text-align: left;
}
#gridLegenda > tbody > tr > td {
  border-right: 1px dotted #f5f5f5;
}
.timeLine-main-header-day-month {
  top: 0px;
  position: sticky;
  flex: 0 0 15px;
  padding: 5px;
  z-index: 90;
}
#gridLegenda .period_15, 
.flat #layerVisualP .period_15 .innerborder, 
.period_15 .cs_i, .titleperiod.period_15 {
  background-color: #bdcc1e;
  background-image: none;
  border-color: #bdcc1e;
}
#gridLegenda .period_23, 
.flat #layerVisualP .period_23 .innerborder, 
.period_23 .cs_i, .titleperiod.period_23 {
  background-color: #3490dc;
  background-image: none;
  border-color: #2779bd;
}
#containerLegenda {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 99;
}
.containerLegendaCol div.row div.period {
  width: 19px;
  height: 17px;
  border-radius: 3px;
  float: left;
}
.period-label {
  margin-left: 30px;
}
.timeLine-main-header-time {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  height: 22px;
  justify-content: stretch;
}

.timeLine-main-header-time-item {
  border-left: solid 1px silver;
  border-bottom: solid 1px silver;
  border-top: solid 1px silver;
  text-align: center;
  padding-top: 5px;
}
/* Main Area Data*/

.timeLine-main-data-viewPort {
  flex: 1 1 auto;
  position: relative;
  overflow: hidden;
  background-color: #fbf9f9;
}
.timeLine-main-data-container {
  position: relative;
  top: 0;
  left: 0;
  height: 100%;
  background-color: rgb(255, 255, 255);
}

.timeLine-main-data-row {
  position: absolute;
  width: 100%;
  height: 50px;
}

.timeLine-main-data-task {
  position: absolute;
  background-color: darkorchid;
  border-radius: 14px;
  color: white;
  text-align: center;
}

.timeLine-main-data-task-side {
  position: absolute;
  width: 10px;
  cursor: col-resize;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: none;
}
.timeLine-main-data-task-side-linker {
  width: 8px;
  height: 8px;
  border-radius: 4px;
  cursor: default;
  z-index: 100;
}
.timeLine-main-data-task-name {
  display: none;
}
.timeLine-main-data-task-side-linker:hover {
  background-color: black;
  border: solid 0.5px grey;
}
/* .timeLine-main-data-task:hover {
    background-color:chocolate;
    border:solid 2px darkorchid;
    cursor: move;
} */

.timeLine-main-data-verticalLine {
  flex: 1 1 auto;
  height: 100%;
  width: 24px;
  background-color: white;
  border-left-width: 0.5px;
  border-left-color: rgb(207, 207, 205);
  border-left-style: dashed;
}

/* Side Area */

.timeLine-side-main {
  flex: 0 0 auto;
  width: 525px;
  min-width: 525px;
  display: flex;
  flex-direction: row;
}

.timeLine-side {
  flex: 1 0 100px;
  display: flex;
  flex-direction: column;
  border-right: solid 1px rgb(207, 207, 205);
}

.verticalResizer {
  flex: 0 0 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: col-resize;
  border-right: solid 1px rgb(207, 207, 205);
  height: 100%;
  z-index: 10;
}
.squareGrip {
  flex: 0 0 auto;

  border-radius: 50%;
  height: 3px;
  width: 3px;
  margin: 3px 0;
}

.timeLine-side-title {
  flex: 0 0 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timeLine-side-task-viewPort {
  position: relative;
  flex: 1 1 auto;
  height: 100%;
  background-color: #fbf9f9;
  overflow-x: hidden;
  overflow-y: auto;
}

.timeLine-side-task-container {
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
}

.timeLine-side-task-row {
  padding: 0 10px;
  position: absolute;
  width: 100%;
  background-color: rgb(112, 112, 112);
  border-bottom-width: 0.5px;
  border-bottom-color: rgb(207, 207, 205);
  border-bottom-style: solid;
  height: 30px;
  color: grey;
  overflow: hidden;
  text-overflow: ellipsis;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
#app .timeLine-side-task-row .labels {
  display: flex;
}
#app .timeLine-side-task-row .labels > div {
  font-size: 10px;
  line-height: 16px;
  text-align: center;
  min-width: auto;
  margin-right: 2px;
}
#app .timeLine-side-task-row .labels > div:last-child {
  margin-right: 0;
}
#app .list-members .members > span {
  font-size: 10px;
  height: 20px;
  width: 20px;
  padding: 0 5px;
  line-height: 20px;
  margin-right: 3px;
}
.timeLine-side-main .timeLine-side-task-container .timeLine-side-task-row.tab {
  background-color:  #6cb2eb !important;
  color: #fff;
  font-weight: 700;
}
.timeLine-side-main .timeLine-side-task-container .timeLine-side-task-row.board {
  background-color: #3f8cca !important;
  color: #fff;
  font-weight: 700;
}
.timeLine-sidebar-wrapper {
  display: flex;
  width: 100%;
}
.timeLine-cart,
.timeLine-label,
.timeLine-member,
.timeLine-start,
.timeLine-end {
  display: flex;
  flex: 1 0 60px;
  flex-direction: column;
}
.timeLine-member {
  flex: 1 0 85px;
}
.timeLine-cart {
  flex: 1 0 220px;
}
.timeLine-start,
.timeLine-end {
  flex: 1 0 80px;
}
.timeLine-header-title {
  line-height: 20px;
  font-weight: 700;
  padding: 0 10px;
}
.header-bottom > div{
  background-color: #EFF8FF;
  color: #000;
  border-color: #ccc !important;
}
.header-top > div {
  background-color: #C6E1F7;
  color: #000;
  text-shadow: none;
  border-color: #acacac !important;
}
#timeLinedataViewPort .timeLine-main-data-row.card {
  background-color: #fff !important;
}
#timeLinedataViewPort .timeLine-main-data-row.card:nth-child(2n) {
  background-color: #F2FBFF !important;
}
#timeLinedataViewPort .timeLine-main-data-row.card > div {
  min-width: 24px;
}
#timeLinedataViewPort .timeLine-main-data-row.tab {
  background-color: #DCEDFA !important;
}
#timeLinedataViewPort .timeLine-main-data-row.board {
  background-color: #3f8cca !important;
}
#timeLinedataViewPort .timeLine-main-data-row.tab > div,
#timeLinedataViewPort .timeLine-main-data-row.board > div {
  display: none;
}
.timeLine-side-main .timeLine-side-task-container .timeLine-side-task-row {
  background-color: #fff;
}
.timeLine-side-main .timeLine-side-task-container .timeLine-side-task-row  > div{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  outline: none;
}
.timeLine-side-main .timeLine-side-task-container .timeLine-side-task-row:nth-child(2n){
  background-color: #F2FBFF;
}
.header-bottom > div {
  position: relative;
}
.header-bottom > div::before {
  border-left: 1px solid #333;
  position: absolute;
  top: 100%;
  left: 0;
  height: 100vh;
}