.wrapper {
	display: block;
    width: 100%;
	overflow: hidden;

	/* Bars */
	.bars {
		background-color: rgba($color: #000000, $alpha: 0.1);
		border-radius: 8px;

		.bar {
			float: left;
			border-radius: 0;
			height: 100%;
		}
	
		.bar:first-child {
			border-top-left-radius: 8px;
			border-bottom-left-radius: 8px;
		}
	
		.bar:last-child {
			border-top-right-radius: 8px;
			border-bottom-right-radius: 8px;
		}

	}

	.values .value {
		float: left;
		text-align: center;
	}

	.scale .graduation {
		float: left;
		text-align: center;
	}

	/* Legends */
	.legends {
		text-align: center;
		font-size: 0.95em;
	}
	
	
	.legends .legend {
		display: inline-block;
		margin: 0 5px;
		text-align: center;
	}
	
	.legends .legend .dot {
		font-size: 25px;
		vertical-align: middle;
	}
	
	.legends .legend .label {
		margin-left: 2px;
		vertical-align: middle;
	}
}

/* Sizes */

.wrapperTiny {
	height: 6px;
}

.wrapperSmall {
	height: 10px;
}

.wrapperMedium {
	height: 14px;
}
